<template>
  <div class="share full-screen">
    <div class="page-panel" v-show="!isLoading">
      <pem-scroll-view ref="scroll" class="scroll-view">
        <div>
          <div style="padding: 0.5rem 0.4rem 0 0.4rem;">
            <div class="share-wrapper">
              <img class="share-info-logo" :src="project.logo"  @error="defaultImage"/>
              <div class="share-info-name">{{ project.name }}</div>
              <div class="share-info-bar">
                <!-- <pem-icon v-if="project.round" name="project_round" width="26" height="28" /> -->
                <span v-if="project.round">{{ project.round }}</span>
                <!-- <pem-icon v-if="project.price" name="project_price" width="26" height="28" /> -->
                <span v-if="project.price">{{ project.price }}</span>
                <!-- <pem-icon v-if="project.proportion" name="project_proportion" width="26" height="26" /> -->
                <span v-if="project.proportion">{{ project.proportion }}%</span>
              </div>
              <div class="share-info-summary">{{ project.summary }}</div>
              <div class="share-info-desc">{{ project.desc }}</div>
              <div class="share-info-btn" @click="gotoMore">
                查看更多
              </div>
            </div>
            <div class="logo"></div>
          </div>
        </div>
      </pem-scroll-view>
    </div>
    <pem-dialog ref="alert" :text="alertText" :confirmBtnText="alertBtnText" :type="alertType" @confirm="alertCallback"></pem-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
import PemIcon from 'components/pem-icon/pem-icon'
import PemDialog from 'components/dialog/dialog'
import PemScrollView from 'components/scroll-view/scroll-view'
import tempLogoUrl from 'common/image/icon-project-Default_graph@2x.png'
import { MiniSiteApi, ImgPath } from 'api'
import { mapState } from 'vuex'
import { setTitle } from 'common/js/util'

export default {
  components: {
    PemIcon,
    PemDialog,
    PemScrollView
  },
  data () {
    return {
      isLoading: true,
      project: {},
      alertText: '',
      alertType: '',
      alertBtnText: '',
      alertBtnCallback: null
    }
  },
  activated () {
    // http://localhost:8080/share?projectzj=96D7B177835E49379B54525361EAC5B4
    let options = {projectzj: this.$route.query.projectzj}
    if (this.$route.query.fapp) options['shareid'] = this.$route.query.fapp
    this._getShareProject(options)
  },
  created () {
    this.initBaidu()
    this._setTitle('投融资信息对接服务平台')
  },
  computed: mapState({auth: state => state.auth}),
  methods: {
    _setTitle (title) {
      setTitle(title)
    },
    gotoMore () {
      let zj = this.$route.query.projectzj
      if (!zj) {
        return false
      }
      if (this.auth && +this.auth.applystatus === 200 && this.auth.type === 'tz') {
        this.$router.push('/index')
        this.$router.push(`/details/${zj}`)
      } else {
        if (this.auth && this.auth.type === 'cy') {
          this.alert('创业者不能查看项目详情')
        } else if (this.auth && this.auth.type === 'cw') {
          this.alert('财务顾问请下载APP登录使用')
        } else if (this.auth && this.auth.type === 'tz') {
          if (+this.auth.applystatus === 0) {
            this.alert('通过投资人认证，即可查看详情', {
              type: 'confirm',
              btn: '立即认证',
              callback: () => {
                this.$router.push('/index')
                this.$router.push('/auth-investor-one')
              }
            })
          } else if (+this.auth.applystatus === 100) {
            this.alert('认证审核中，请耐心等待或联系客服：010-84535220转8251', {
              type: 'confirm',
              btn: '联系客服',
              callback: () => {
                window.location.href = 'tel://010-84535220'
              }
            })
          } else {
            this.alert('认证失败，暂不可查看详情', {
              type: 'confirm',
              btn: '重新认证',
              callback: () => {
                this.$router.push('/index')
                this.$router.push('/auth-investor-situation')
              }
            })
          }
        } else if (this.auth && this.auth.type === 'yk') {
          this.$router.push('/index')
          this.$router.push('/auth')
        } else {
          this.$router.push('/index')
          this.$router.push(`/login?go=details&zj=${zj}`)
        }
      }
    },
    alert (text, options = {}) {
      this.alertText = text
      this.alertType = options.type || 'alert'
      this.alertBtnText = options.btn || '确定'
      this.alertBtnCallback = options.callback || null
      this.$refs.alert.show()
    },
    alertCallback () {
      this.alertBtnCallback && this.alertBtnCallback()
    },
    defaultImage (e) {
      e.target.src = tempLogoUrl
    },
    initBaidu () {
      var _hmt = _hmt || []
      var hm = document.createElement('script')
      hm.src = 'https://hm.baidu.com/hm.js?1ca4aeb24e1e451899a7199ac90c6da1'
      var s = document.getElementsByTagName('script')[0]
      s.parentNode.insertBefore(hm, s)
    },
    _getShareProject (options) {
      MiniSiteApi.getShareProject(options).then(data => {
        if (Number(data.resultcode) === 1) {
          this.project = {
            name: data.resultList.name || '',
            logo: data.resultList.logo ? (ImgPath + data.resultList.logo) : null,
            round: data.resultList.turnzj || '',
            summary: data.resultList.summarymemo || '',
            desc: data.resultList.recommend || '',
            price: data.resultList.currencytype && data.resultList.raisefunds ? `${data.resultList.currencytype}${data.resultList.raisefunds}万` : null,
            proportion: data.resultList.stockratio || null
          }
          this.$nextTick(() => {
            this.$refs.scroll.refresh()
          })
        }
        this.wxShare({title: '【' + this.project.name + '】' + this.project.summary, content: this.project.desc, imgUrl: this.project.logo})
        this.isLoading = false
      }).catch((error) => {
        this.isLoading = false
        console.log(error)
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  $color-black = #202020
  .share
    font-size: 0.24rem
    background-color: $color-black
    width:7.5rem
    .page-panel
      
      overflow: auto
    .share-wrapper
      width: 6.7rem
      background-color: #ffffff
      border-radius: 0.08rem
      text-align: center
      bg-image('xiangmu_bg')
      background-repeat: no-repeat
      background-size: 6.7rem 2.28rem
      background-position: top left
      padding-bottom: 0.5rem
      .share-info-logo
        width: 1.28rem
        height: 1.28rem
        margin: 1.42rem auto 0 auto
        display: inline-block
      .share-info-name
        font-size: 0.4rem
        color: $color-black
        letter-spacing: -0.0096rem
        font-weight: bold
        margin-top: 0.12rem
      .share-info-bar
        margin-top: 0.16rem
        font-size: 0
        padding-bottom: 0.32rem
        position: relative
        border-bottom: 0.02rem dashed #d8d8d8
        i, span
          vertical-align: middle
        span
          display: inline-block
          font-size: 0.24rem
          color:  #6F6F6D
          height: 0.4rem
          line-height: 0.4rem
          background: #f6f6f6
          padding: 0 0.2rem
          &:nth-child(2)
            margin-left: 0.4rem
            margin-right: 0.4rem
        &:before, &:after
          content: ''
          display: block
          position: absolute
          width: 0.72rem
          height: 0.72rem
          background-color: $color-black
          border-radius: 0.72rem
          bottom: -0.36rem
        &:before
          left: -0.4rem
        &:after
          right: -0.4rem
      .share-info-summary
        font-size: 0.32rem
        color: $color-black
        letter-spacing: -0.0002rem
        font-weight: bold
        margin-top: 0.5rem
      .share-info-desc
        font-size: 0.28rem
        color: #a7a7a7
        letter-spacing: -0.0018rem
        line-height: 0.4rem
        margin: 0.3rem 0.4rem 0 0.4rem
        text-align: left
        min-height: 2.28rem
      .share-info-btn
        width: 4.8rem
        height: 0.8rem
        line-height: 0.8rem
        border-radius: 0.08rem
        color: $color-black
        font-size: 0.32rem
        letter-spacing: -0.0064rem
        background-color: $color-glod
        margin: 0.4rem auto 0 auto
    .logo
      width: 100%
      height: 0.54rem
      bg-image('icon-logo')
      background-size: 1.88rem 0.54rem
      background-repeat: no-repeat
      padding: 0.68rem 0 0.2rem 0
      background-position: center
</style>



